import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';

import * as Parse from 'parse';
import VoucherStore from '../../store/voucher.store';
import {ModalHelper} from '../../helper/modal.helper';
import {IVoucher, IVoucherBrand} from '../../model/voucher.model';
import {UserStore} from '../../store/user.store';
import {IUser} from '../../model/user.model';
import {Chart, PieController, ArcElement, DoughnutController, Legend} from 'chart.js';
import VoucherBrandStore from "../../store/voucherBrand.store";
import {ViewDidEnter} from "@ionic/angular";
import ChartStore from "../../store/chart.store";
import VoucherRequestStore from "../../store/voucherRequest.store";
import SoldStore from "../../store/sold.store";

@Component({
  selector: 'app-voucher',
  templateUrl: './voucher.page.html',
  styleUrls: ['./voucher.page.scss'],
})
export class VoucherPage implements OnInit, AfterViewInit, ViewDidEnter {
  @ViewChild('doughnutChart') public doughnutChart;

  user: Parse.Object;


  chart: Chart;

  constructor(public voucherRequestStore: VoucherRequestStore, public chartStore: ChartStore, public voucherBrandStore: VoucherBrandStore, public userStore: UserStore, public voucherStore: VoucherStore, private modalHelper: ModalHelper, public soldStore: SoldStore) {
    Chart.register(DoughnutController, ArcElement);
    this.user = Parse.User.current();
    this.user.fetch().then(r => {
      this.userStore.setUser(this.user.toJSON() as unknown as IUser);
    });
  }

  async selectedVoucherBrand(voucherBrand: IVoucherBrand) {
    this.voucherBrandStore.setSelectedVoucherBrand(voucherBrand);
    await this.modalHelper.openVoucherModal();
  }

  ngOnInit() {

  }

  ionViewDidEnter(): void {
    this.chart = new Chart(this.doughnutChart.nativeElement, {
      type: 'doughnut',
      plugins: [Legend],
      data: {
        datasets: [{
          label: 'Sum of points',
          data: [this.userStore.user.points, this.userStore.user.reserved_points],
          backgroundColor: [
            'rgb(54, 162, 235)',
            'rgb(255, 99, 132)',
          ],
        }],
        labels: ['Reserved', 'Available'],
      },

    });
    this.chartStore.setChart(this.chart);
  }

  ngAfterViewInit(): void {

  }


}
